let _this = this;
export default {
	
	data() {
		return {
			br:'\n',
			aptDetail:{
				aptId:0,
				pub_uid:0,
				title:'',
				datetime:'',
				address:'',
				ask:'',
				startDatetime:'',
				aptTypeId:'',
				image:''
			},

			
			btnDisable:false
			
		}
	},

	onLoad: function(option){	
		uni.showLoading({
			title:'加载数据中'
		});
		this.aptId = option.apid;
		this.getAppointmentDetail();
		
	},
	onShow:function(options){

	},
	
	methods:{
		getAppointmentDetail:function(){
			let url = this.createApiUrl('appointment/get/one?appointmentId=' + this.aptId);
			
			uni.request({
				url: url,
				method: 'GET',
				data: {},
				success: res => {
					uni.hideLoading();
					res = res.data.data;
					console.log(res);
					
					this.aptDetail.title = res.title;
					this.aptDetail.ask = res.ask;
					this.aptDetail.datetime = res.date;
					this.aptDetail.address = res.address;
					this.aptDetail.startDatetime = res.startDatetime;
					this.aptDetail.aptTypeId = res.aptTypeId;
					this.aptDetail.pub_uid = res.userId;
					this.aptDetail.image = res.image;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		addOrder:function(){
			let userinfo = this.getUserInfo();
			let _pubUid = this.aptDetail.pub_uid;
			let _acptUid = userinfo.id;
			let _aptId = parseInt(this.aptDetail.aptId);
			
			let sendData = {
				pub_uid:_acptUid,
				acpt_uid: _acptUid,
				apt_id:_aptId,
				evaluate:'',
				date:'',
				image:''
			};
			console.log(sendData);
			
			
			uni.request({
				url: this.createApiUrl('/order/add'),
				method: 'POST',
				header: {
				        'content-type': 'application/x-www-form-urlencoded'  
				},
				data: sendData,
				success: res => {
					res = res.data;
					console.log(res);
					if(res.code == 200){
						uni.showToast({
							title:'预约成功',
						});
						this.btnDisable = true
					}else{
						uni.showToast({
							title:'网络开小差了！，请稍候再试',
						});
					}
					
					
				},
				fail: () => {},
				complete: () => {}
			});
		}
		
	}
}
